<template>
  <div class="g-nosuccessed">
    <div class="m-img">
      <img src="../assets/images/nosuccessed.png" alt="">
    </div>
    <div class="m-msg">订单支付失败</div>
    <div class="m-content">
      <div class="s-box">
        <span>订单编号：</span>
        <span>{{orderList.orderNumber}}</span>
      </div>
      <div class="s-box">
        <span>应付金额：</span>
        <span>¥{{orderList.orderMoney}}</span>
      </div>
      <div class="s-box">
        <span>收货人：</span>
        <span>{{orderList.contact}}</span>
      </div>
      <div class="s-box">
        <span>联系号码：</span>
        <span>{{orderList.phone}}</span>
      </div>
      <div class="s-box">
        <span>收货地址：</span>
        <span>{{orderList.province}}{{orderList.city}}{{orderList.area}}{{orderList.address}}</span>
      </div>
      <div class="s-box">
        <span>备注信息：</span>
        <span>{{orderList.remark||'无'}}</span>
      </div>
    </div>
    <div class="m-button">
      <router-link :to="{name:'home'}" tag="span" replace>返回首页</router-link>
      <router-link :to="{name:'orderDetials',query:{orderid:this.$route.query.orderId}}" tag="span" replace>查看订单</router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: "nosuccessed",
    data(){
      return {
        orderList:{}
      }
    },
    created(){
      this.nosuccessed();//成功信息
    },
    methods:{
      nosuccessed(){
        this.$post(this.$bee+'/api/order/orderDetail',{id:this.$route.query.orderId}).then((data)=>{
          if(data.code==200){
            this.orderList=data.data.order;
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      }
    }
  }
</script>

<style scoped lang="stylus">
  .g-nosuccessed
    padding 0 15px
    .m-img
      width 70px
      height 70px
      margin 20px auto 0
      img
        width 100%
        height 100%
    .m-msg
      margin 13px auto 0
      color: #ea3323;
      font-size 14px
      margin-bottom 35px
    .m-content
      .s-box
        font-size 14px
        text-align left
        margin-bottom 20px
        span
          color: #8b8383
          display inline-block
          vertical-align top
          &:nth-child(1)
            width 80px
          &:nth-child(2)
            width calc(100% - 80px)
    .m-button
      margin-top 85px
      span
        display inline-block
        width: 90px;
        height: 35px;
        line-height 35px
        background-color: #ffa243;
        border-radius: 5px;
        font-size 18px
        color: #ffffff;
        margin 0 22.25px
</style>
